import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { user } from 'src/app/shared/models/product';
import { AuthLoginService } from 'src/app/shared/services/auth-login.service';
import { CartService } from 'src/app/shared/services/cart.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  LoginForm!: FormGroup;
  constructor(
    // public ServicData: CartService,
    private router: Router,
    private auth: AuthLoginService
  ) {}

  async ngOnInit(): Promise<void> {
// this.userlogin=await this.ServicData.getAll()
    this.LoginForm = new FormGroup({
      email: new FormControl('', [
        Validators.required,
        Validators.email,
        Validators.minLength(5),
      ]),

      password: new FormControl('', [
        Validators.required,
        Validators.maxLength(16),
        Validators.minLength(6),
        this.isValidPassword,
      ]),
    });
  }

  isValidPassword(control: AbstractControl): any {
    debugger;
    if (control.value) {
      const regex = /^[A-Z]/  ;
      if (!regex.test(control.value)) {
        return { invalidPassword: true };
      }
    }
  }

  login() {

    if (this.LoginForm.valid) {
      this.auth.doLogin();
      this.router.navigate(['/landing']);
    }


    }



  }


