
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {  user } from '../models/product';



@Injectable({
  providedIn: 'root'
})
export class CartService {
  [x: string]: any;
  public items: Array<user> = [{}];
  public endPointApi:string = "https://60ada94d80a61f0017331727.mockapi.io/user";
  constructor(private http:HttpClient) { }

  async getAll():Promise<Array<user >>
  {
    return this.http.get(this.endPointApi).toPromise<any>();
  }


  async AddItem(product:user ):Promise<user >
  {
    return this.http.post(this.endPointApi,product).toPromise<any>();

  }

  async DelItem(id:string):Promise<user>
  {
    return  this.http.delete(this.endPointApi+id).toPromise<any>();

  }

  async UpdItem(id:string):Promise<user>
  {
    debugger;
    return this.http.put(this.endPointApi+id,CartService).toPromise<any>();

  }





}



